import React, { useState, useEffect } from "react";
import { blog } from "../../dummydata";

const BlogCard = ({ filterValue }) => {
  const [filteredBlog, setFilteredBlog] = useState([]);
  const [visibleCount, setVisibleCount] = useState(4); //     

  //  
  const handleFilter = (value) => {
    const filtered = blog.filter((item) => item.date === value);
    setFilteredBlog(filtered);
  };

  // filterValue    
  useEffect(() => {
    setVisibleCount(4);
    if (filterValue !== "") {
      handleFilter(filterValue);
    } else {
      setFilteredBlog(blog);
    }
  }, [filterValue]);

  // ' '       4  
  const handleShowMore = () => {
    setVisibleCount(visibleCount + 4);
  };

  //         
  const handleBlogClick = (url) => {
    if (url) {
      window.open(url, "_blank");
    } else {
      alert("  URL .    .\n   . -  ");
    }
  };

  return (
    <>
      {filteredBlog.slice(0, visibleCount).map((val) => (
        <div
          className="items shadow"
          style={{ width: "48%", float: "left", marginRight: "10px", cursor: "pointer" }} //    
          key={val.id}
          onClick={() => handleBlogClick(val.url)} //      
        >
          <div className="img">
            <img src={val.cover} alt="" />
          </div>
          <div className="text">
            <div className="admin flexSB">
              <span>
                <i className="fa fa-user"></i>
                <label htmlFor="">{val.type}</label>
              </span>
              <span>
                <i className="fa fa-map-marker-alt"></i>
                <label htmlFor="">{val.date}</label>
              </span>
            </div>
            <h1>{val.title}</h1>
            <p>{val.desc}</p>
          </div>
        </div>
      ))}
      <div style={{clear:"both"}} />
      {/* ' '  */}
      {filteredBlog.length > visibleCount && (
        <button className="primary-btn" onClick={handleShowMore}> </button>
      )}
    </>
  );
};

export default BlogCard;
